export const INITIAL_AMOUNT: number = 1_200_000;

export const INITIAL_TERM: number = 36;

export const BUSINESS_PERCENT: number = 0.2; // 20%

export const DEBOUNCE_TIME: number = 300;

export type ModeType = 'private' | 'business';

export interface RangeSliderProps {
  title: string;
  min: number;
  max: number;
  step: number;
  unit: string;
  minLabel: string;
  maxLabel: string;
  value: number;
  onChange: (value: number) => void;
}

interface GetSliderParams {
  amount: number;
  term: number;
  setAmount: React.Dispatch<React.SetStateAction<number>>;
  setTerm: React.Dispatch<React.SetStateAction<number>>;
}

export const getRangeSliders = (
  {
    amount,
    term,
    setAmount,
    setTerm,
  }: GetSliderParams,
): RangeSliderProps[] => [
  {
    title: ' ',
    min: 200_000,
    max: 4_000_000,
    step: 100_000,
    unit: '',
    minLabel: ' 200 ',
    maxLabel: ' 4 ',
    value: amount,
    onChange: setAmount,
  },
  {
    title: ' ',
    min: 6,
    max: 36,
    step: 1,
    unit: '.',
    minLabel: ' 6 .',
    maxLabel: ' 3 ',
    value: term,
    onChange: setTerm,
  },
];
